.PyramidControl {
  background: #f8f7f6;
  border-radius: 0 0 0 0;
  font-size: 15px; }
  .PyramidControl::after {
    clear: both;
    content: "";
    display: table; }

.PyramidControl__Controls {
  float: right;
  font-size: 0;
  padding: 0 10px;
  background: #f8f7f6; }

.PyramidControl__Control {
  background: transparent;
  border: 0;
  color: #aaa;
  display: inline-block;
  font-size: 15px;
  padding: 12px 8px;
  cursor: pointer;
  user-select: none;
  transition: color .3s ease-out; }
  .PyramidControl__Control:hover {
    color: #888; }

.PyramidControl__Control--Remove:hover {
  color: #d00; }

.PyramidControl__Title {
  position: relative;
  padding: 12px 15px;
  float: left; }
  .has-icon .PyramidControl__Title {
    padding-left: 50px; }

.PyramidControl__TitleIcon {
  width: 24px;
  height: 24px;
  margin-right: .75rem;
  position: absolute;
  left: 1rem;
  top: 50%;
  margin-top: -12px; }

.PyramidControl__TitleIcon img,
.PyramidControl__TitleIcon svg {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto; }

.PyramidLabel {
  margin-bottom: .5rem;
  display: block; }

.PyramidContainer {
  max-width: 800px;
  padding-bottom: 2rem;
  margin-left: auto;
  margin-right: auto; }

@media (min-width: 1200px) {
  .PyramidContainer {
    padding-top: 2rem; } }

.PyramidAdd {
  position: relative;
  padding: 16px 0;
  display: block;
  border: 0;
  width: 100%;
  color: #666;
  background: rgba(255, 255, 255, 0.25);
  opacity: 0;
  transition: opacity .3s ease-out; }
  .PyramidAdd:hover {
    opacity: 1; }

.PyramidAdd__Handle {
  position: absolute;
  left: 50%;
  top: 0;
  text-align: center;
  line-height: 32px; }

.PyramidBlock__Content {
  background: #fff;
  padding: 0 16px; }

.PyramidBlock__ContentGroup {
  margin-left: -16px;
  margin-right: -16px;
  padding: 16px;
  border-bottom: 1px solid #ddd; }
  .PyramidBlock__ContentGroup:last-child {
    border-bottom: 0; }

.PyramidBlock__Content--nopadding {
  padding: 0; }

.PyramidBlock__Content--nopadding > .mce-tinymce {
  border: 0; }

.PyramidBlock__Content .mce-content-body {
  margin: 1rem; }

.PyramidBlock__Group {
  margin-bottom: 1rem; }

.PyramidBlockWrapper:last-child .PyramidAdd,
.PyramidAdd--Large {
  opacity: 1;
  background: transparent; }

.PyramidBlockWrapper:last-child .PyramidAdd__Handle,
.PyramidAdd--Large .PyramidAdd__Handle {
  position: static;
  margin: 0 auto;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  background: #fff; }

.PyramidBlockWrapper-enter {
  opacity: 0.01;
  transform: translateX(30px); }

.PyramidBlockWrapper-enter.PyramidBlockWrapper-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-out, transform 500ms ease-out;
  transform: translateX(0); }

.PyramidBlockWrapper-leave {
  opacity: 1;
  transform: translateX(0); }

.PyramidBlockWrapper-leave.PyramidBlockWrapper-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-out, transform 300ms ease-out;
  transform: translateX(-30px); }

.PyramidFormLabel {
  display: block;
  margin-bottom: .25rem; }

.PyramidFormControl {
  width: 100%;
  height: 38px;
  display: block;
  border-radius: 3px;
  border: 1px solid #bbb;
  padding: 14px; }

textarea.PyramidFormControl {
  resize: vertical;
  line-height: 1.4;
  height: auto; }

.PyramidVideoPreview {
  display: table;
  padding-top: 1rem; }

.PyramidVideoPreview > * {
  display: table-cell;
  vertical-align: middle; }

.PyramidVideoPreview__Thumbnail {
  position: relative;
  max-width: 140px;
  color: #fff;
  text-decoration: none; }
  .PyramidVideoPreview__Thumbnail:hover, .PyramidVideoPreview__Thumbnail:focus {
    color: #fff; }

.PyramidVideoPreview__ThumbnailImage {
  max-width: 100%;
  display: block; }

.PyramidVideoPreview__ThumbnailOverlay {
  text-align: center;
  padding: 0 1rem;
  background: rgba(10, 10, 10, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity .3s ease-out;
  user-select: none; }

.PyramidVideoPreview__ThumbnailOverlay div {
  position: relative;
  top: 50%;
  transform: translateY(-50%); }

.PyramidVideoPreview__ThumbnailOverlay .fa {
  display: block;
  margin-bottom: 8px;
  font-size: 14px; }

.PyramidVideoPreview__Thumbnail:hover .PyramidVideoPreview__ThumbnailOverlay {
  opacity: 1; }

.PyramidVideoPreview__Content {
  padding-left: 1rem; }

.PyramidVideoPreview__ContentLabel {
  color: #aaa;
  margin-top: .5rem;
  margin-bottom: .125rem;
  text-transform: uppercase;
  font-size: 12px; }
  .PyramidVideoPreview__ContentLabel:first-child {
    margin-top: 0; }

.PyramidPicker {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  max-width: 320px;
  background: #fff;
  box-shadow: -1px 0 5px rgba(20, 20, 20, 0.3);
  z-index: 9999; }

.PyramidPicker,
.PyramidPicker__Bg {
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease-out, visibility .3s ease-out; }
  .PyramidPicker.is-visible,
  .PyramidPicker__Bg.is-visible {
    opacity: 1;
    visibility: visible; }

.PyramidPicker__Bg {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 9998;
  background: rgba(10, 10, 12, 0.6);
  border: 0; }

.PyramidPicker__Button {
  background: transparent;
  display: block;
  width: 100%;
  border: 0;
  text-align: center;
  padding: 1rem 0.5rem;
  border-bottom: 1px solid #ddd; }
  .PyramidPicker__Button:hover {
    background-color: #fafafa; }

.PyramidPicker__Close {
  position: absolute;
  bottom: 100%;
  right: 0;
  background: transparent;
  border: 0; }

.PyramidImage__Dropzone {
  width: 100%;
  padding: 2rem;
  border: 2px dashed #ddd;
  text-align: center; }
  .PyramidImage__Dropzone:hover {
    border: 2px dashed #aaa; }
  .PyramidImage__Dropzone:last-child {
    margin-bottom: 0; }

.PyramidImageList {
  background-color: #f6f6f6;
  border: 1px solid #ddd;
  margin-top: -2px; }

.PyramidImageList__Row {
  position: relative;
  margin: 0;
  padding: 1rem;
  border-bottom: 1px solid #ddd; }
  .PyramidImageList__Row::after {
    clear: both;
    content: "";
    display: table; }
  .PyramidImageList__Row:last-child {
    margin-bottom: 0;
    border-bottom: 0; }

.PyramidImageList__Thumb {
  position: relative;
  border: 1px solid #ddd;
  width: 120px;
  height: 120px;
  float: left;
  background-color: #fff; }

.PyramidImageList__Image {
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.PyramidImageList__Content {
  padding-left: 135px;
  max-width: 500px; }

.PyramidImageList__Controls {
  position: absolute;
  top: 0;
  right: 0; }

.PyramidImageList__Control {
  display: inline-block;
  vertical-align: top;
  background: #fff;
  width: 40px;
  height: 40px;
  border: 1px solid #ddd;
  border-top: 0;
  border-right: 0;
  color: #888; }
  .PyramidImageList__Control:hover {
    background-color: #eee; }

.PyramidDebug__Button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: 0;
  background: transparent;
  color: #fff;
  margin-top: 30px;
  opacity: .5; }

.PyramidDebug__Code {
  background: #fff;
  color: #333;
  padding: 1rem; }
